@use '../../../../styles/vars';

/* section margin bottom */
.bio {
    span {
        display: inline-block;
    }

    /* HEEY text */
    .title {
        margin-bottom: vars.$mg;
        letter-spacing: -3px;
    }
}

/* emoji */
.bio__emoji {
    display: block;
    width: 50px;
    margin-left: vars.$mg-sm;
    transform: translateY(20%);
}
